<template>
<div class="content-wrapper">
  <el-breadcrumb separator="/">
    <el-breadcrumb-item :to="{ path: '/' }">首页</el-breadcrumb-item>
    <el-breadcrumb-item>{{$route.params.sub}}</el-breadcrumb-item>
    <el-breadcrumb-item>{{$route.params.id}}</el-breadcrumb-item>
  </el-breadcrumb>

  <div class="ctrl-bar">
    <el-button-group v-if="toolState=='1'">
      <el-button type="default" icon="fal fa-check-square"> 全选</el-button>
      <el-button type="default" icon="el-icon-share"> 导出</el-button>
      <el-button type="default" icon="fal fa-chart-bar"> 统计</el-button>
      <el-button type="danger" icon="el-icon-delete">删除</el-button>
    </el-button-group>
    <div class="input-wrapper" v-if="toolState=='2'">
      <el-select v-model="selectType" slot="prepend" placeholder="阶段" class="w100">
        <el-option label="前期" value="1"></el-option>
        <el-option label="实施" value="2"></el-option>
        <el-option label="提前" value="3"></el-option>
        <el-option label="验收" value="4"></el-option>
        <el-option label="质保" value="5"></el-option>
      </el-select>
      <el-autocomplete class="input-client w200" prefix-icon="fal fa-user" v-model="inputClient" :fetch-suggestions="querySearchClient" placeholder="客户名称" @select="handleSelect"></el-autocomplete>
      <el-date-picker v-model="inputSEDate" type="daterange" align="right" unlink-panels range-separator="至" start-placeholder="开始日期" end-placeholder="结束日期" :picker-options="pickerOptions"></el-date-picker>
      <el-autocomplete class="input-client w200" prefix-icon="el-icon-search" v-model="inputTitle" :fetch-suggestions="querySearchTitle" placeholder="项目名称" @select="handleSelect"></el-autocomplete>
      <el-button type="primary" icon="el-icon-search"></el-button>
    </div>
    <el-radio-group class="tool-toggle" v-model="toolState">
      <el-radio-button label="1"><i class="fal fa-square"></i> 选择</el-radio-button>
      <el-radio-button label="2"><i class="fal fa-search"></i> 搜索</el-radio-button>
    </el-radio-group>
  </div>

  <el-table :data="project" style="width: 100%" max-height="500" stripe @selection-change="handleSelectionChange" class="table-list">
    <el-table-column type="selection" v-if="toolState=='1'" width="50">
    </el-table-column>
    <el-table-column prop="id" label="序号" width="50" fixed>
    </el-table-column>
    <el-table-column prop="title" label="项目名称" fixed width="300">
    </el-table-column>
    <el-table-column prop="type" label="类型" width="100" sortable>
    </el-table-column>
    <el-table-column prop="stage" label="阶段" width="100" sortable>
    </el-table-column>
    <el-table-column prop="succBidder" label="中标单位" width="100">
    </el-table-column>
    <el-table-column prop="MIS" label="MIS号" width="250">
    </el-table-column>
    <el-table-column prop="itemAmount" label="项目金额" width="100">
    </el-table-column>
    <el-table-column prop="clientName" label="客户名称" width="300">
    </el-table-column>
    <el-table-column prop="seller" label="销售" width="100">
    </el-table-column>
    <el-table-column prop="salesman" label="商务" width="100">
    </el-table-column>
    <el-table-column prop="PST" label="开始时间" width="200" sortable>
    </el-table-column>
    <el-table-column prop="PET" label="结束时间" width="200" sortable>
    </el-table-column>
    <el-table-column prop="desc" label="备注" width="200">
    </el-table-column>
    <el-table-column fixed="right" label="操作" width="180">
      <template slot-scope="scope">
        <el-button @click="tableRowBtnView(scope.row)" type="text" size="small">查看</el-button>
        <el-button @click="dialogPopup(scope.row,'NewTask')" type="text" size="small">发布任务</el-button>
        <el-button @click="dialogPopup(scope.row,'NewDoc')" type="text" size="small">上传文档</el-button>
      </template>
    </el-table-column>
  </el-table>
  <el-pagination @size-change="handleSizeChange" @current-change="handleCurrentChange" :current-page="currentPage" :page-sizes="[100, 200, 300, 400]" :page-size="100" layout="total, sizes, prev, pager, next, jumper" :total="400"></el-pagination>
  <v-dialog :dialog.sync="dialog" @dialogClose="dialogClose"></v-dialog>
</div>
</template>

<script>
import Dialog from "cps/features/project/dialog/index-dialog.vue"
export default {
  data() {
    return {
      pickerOptions: {
        shortcuts: [{
          text: '最近一周',
          onClick(picker) {
            const end = new Date();
            const start = new Date();
            start.setTime(start.getTime() - 3600 * 1000 * 24 * 7);
            picker.$emit('pick', [start, end]);
          }
        }, {
          text: '最近一个月',
          onClick(picker) {
            const end = new Date();
            const start = new Date();
            start.setTime(start.getTime() - 3600 * 1000 * 24 * 30);
            picker.$emit('pick', [start, end]);
          }
        }, {
          text: '最近三个月',
          onClick(picker) {
            const end = new Date();
            const start = new Date();
            start.setTime(start.getTime() - 3600 * 1000 * 24 * 90);
            picker.$emit('pick', [start, end]);
          }
        }]
      },
      toolState: "2",
      project: [],
      inputTitle: "",
      inputClient: "",
      inputSEDate: "",
      selectType: "",
      selectClient: "",
      multipleSelection: [],
      projectClientList: [],
      projectTitleList: [],
      currentPage: 1,
      dialog:{
        show:false,
        form:"",
        content:""
      }

    };
  },
  components:{
    "v-dialog":Dialog
  },
  watch: {
    toolState: function() {
      console.log(this.toolState);
    }
  },
  methods: {
    dialogPopup(content,form){
      this.dialog.show=true;
      this.dialog.form=form;
      this.dialog.content=content;
    },
    dialogClose(val){
      this.dialog.show=val;
    },
    handleSizeChange(val) { //翻页条目数量改变
      console.log(`每页 ${val} 条`);
    },
    handleCurrentChange(val) { //翻页当前页改变
      console.log(`当前页: ${val}`);
    },
    querySearchClient(queryString, cb) { //搜索条件-客户名提示
      //console.log(this.client);
      var list = this.projectClientList;
      var results = queryString ? list.filter(this.createFilter(queryString)) : list;
      // 调用 callback 返回建议列表的数据
      cb(results);
    },
    querySearchTitle(queryString, cb) { //搜索条件-项目标题提示
      //console.log(this.client);
      var list = this.projectTitleList;
      var results = queryString ? list.filter(this.createFilter(queryString)) : list;
      // 调用 callback 返回建议列表的数据
      cb(results);
    },
    createFilter(queryString) { //搜索条件-建立过滤
      return (list) => {
        return (list.value.toLowerCase().indexOf(queryString.toLowerCase()) === 0);
      };
    },
    handleSelect(item) { //搜索条件-提示选择
      console.log(item);
    },
    handleSelectionChange(val) { //表格选择控制
      this.multipleSelection = val;
    },
    tableRowBtnView(row) { //表格行 查看按钮
      this.$router.push({
        path: '/project/formView/' + row.id
      })
    },
    getData() {
      var url = this.$urls.project;
      this.$http.get(url).then(
        response => {
          var data = JSON.parse(response.bodyText);
          //console.log(data.project.list);
          this.project = data.project.list;
          var _project = data.project.list;
          for (var i = 0; i < _project.length; i++) {
            this.projectClientList.push({
              "value": _project[i].clientName
            })
            this.projectTitleList.push({
              "value": _project[i].title
            })
          }
        },
        error => {
          //console.log(err);
        }
      );
    }
  },
  mounted() {
    this.getData();
  }
};
</script>

<style lang="less">
</style>



