<template>
  <el-card shadow="hover" class="portlet-card">
    <div slot="header" class="clearfix">
      <span class="title">项目统计</span>
      <el-button class="ctrl-btn" type="text">查看列表 <i class="fal fa-angle-double-right"></i></el-button>
    </div>
    <div id="chart-stati" style="height:280px;"></div>
  </el-card>
</template>

<script>

//import echarts from 'echarts';
import echarts from "static/lib/echarts.min.js"
import $c from "static/lib/ayincolor-vue.js"
 
export default {
  data() {
    return {}
  },
  methods: {},
  mounted() {
		//this.$timer.set("test",()=>{
		//},3000)
		//console.log($c.rel5);
		var titleFS="16"
    //级别维度分布 start
    var echart1 = echarts.init(document.getElementById('chart-stati'));
    var option1 = {
        
        tooltip : {
            trigger: 'axis'
        },
        legend: {
            data:['电信','网通','其他'],
            textStyle:{
                color:$c.gyd5
            }
        },
        toolbox: {
            feature: {
                saveAsImage: {},
                 magicType: {
                  type: ['line', 'bar']
                }
            }
        },
        
        grid: {
            left: '2%',
            right: '3%',
            bottom: '3%',
            top:"18%",
            containLabel: true
        },
        
        xAxis : [
            {
                type : 'category',
                boundaryGap : false,
                data : ['一月','二月','三月','四月','五月','六月','七月','八月','九月','十月','十一月','十二月'],
                axisLine: {
                    lineStyle: {
                        color:$c.gyl3
                    },
                },
                axisTick: {
                    lineStyle: {
                        color:$c.gyl3
                    },
                },
                splitLine:{
                    lineStyle: {
                        color: $c.gyl3
                    },
                },
                axisLabel : {
                    textStyle : {
                        color :$c.gyd5,
                        align : 'center'
                    }
                }
            }
        ],
        yAxis : [
            {
                type : 'value',
                axisLine: {
                    lineStyle: {
                        color:$c.gyl3
                    },
                },
                axisTick: {
                    lineStyle: {
                        color:$c.gyl3
                    },
                },
                splitLine:{
                    lineStyle: {
                        color: $c.gyl3
                    },
                },
                axisLabel : {
                    textStyle : {
                        color :$c.gyd5,
                        align : 'right'
                    }
                }
            }
        ],
        series : [
            {
                name:'电信',
                type:'line',
                stack: '总量',
                smooth: true,
                areaStyle: {normal: {}},
                itemStyle:{normal:{color:$c.blhl6}},
                data:[100, 200, 400, 500, 400, 200, 100, 100, 200, 400, 200,100]
            },
            {
                name:'网通',
                type:'line',
                stack: '总量',
                smooth: true,
                areaStyle: {normal: {}},
                itemStyle:{normal:{color:$c.blhl4}},
                data:[100, 200, 300, 400, 300, 200, 50, 50, 100, 200, 100,50]
            },
            {
                name:'其他',
                type:'line',
                stack: '总量',
                smooth: true,
                areaStyle: {normal: {}},
                itemStyle:{normal:{color:$c.blhl2}},
                data:[ 200, 300, 400, 300, 200, 50, 50, 100, 100, 100,50,50]
            }
        ]
    };
		
		this.$timer.set("echart1",()=>{
			echart1.setOption(option1);
    	echart1.resize();	
		},1000)



  }
}
</script>

<style lang="less">

</style>



