<template>
	<div :class="[$route.path=='/login'?'login-logo':'logo-wrapper', $vuex.state.switch.logoHover?'logo-moreInfo':'']" >
  		<img :src="$vuex.state.header.logo.img" @mouseenter="logoMEnter()" @mouseleave="logoMLeave()" class="logo" alt="" />
  		<div class="sys-name">
  			<p class="chs">{{$vuex.state.header.logo.chs}}</p>
  			<p class="eng">{{$vuex.state.header.logo.eng}}</p>
  		</div>
  	</div>
</template>

<script>
export default {
  props: ['userInfo','message'],
  data () {
    return {
      
    }
  },
  computed: {
  },
  methods:{
  	logoMEnter(){
  		this.$timer.set("logoHover",()=>{
  			this.$vuex.state.switch.logoHover=true;
  		},1000)
  	},
  	logoMLeave(){
  		this.$timer.set("logoHover",()=>{
  			this.$vuex.state.switch.logoHover=false;
  		},500)
  	}
  },
  mounted(){
  	//console.log(this.$route.path);
  }
}
</script>



