<template>
<div class="login-wrapper" ref="particles">
  <div class="stage">
    <v-logo></v-logo>
    <div class="tab-content">
      <div class="tabpanel" :class="tabActive=='user'?'active':''">
        <ul class="input-wrap">
          <li class="user-wrap">
            <i class="input-icon fal fa-user"></i>
            <div class="assist-btn rem-btn" @click="remUser()" :class="rem.user?'active':''">
              <el-tooltip class="item" effect="dark" content="记住用户名" :open-delay="1000" placement="right">
                <i class="fa fa-check"></i>
              </el-tooltip>
            </div>
            <!--this.$refs.inputPass.focus()-->
            <input type="text" ref="inputUser" placeholder="请输入用户名" @keyup.tab="	userpassLogin()" v-model="inputs.user"/>
                    </li>
          <li class="pass-wrap">
            <input type="password" ref="inputPass" placeholder="请输入密码" @keyup.enter="userpassLogin()"  v-model="inputs.pass"/>
            <i class="input-icon fal fa-lock"></i>
            <div class="assist-btn rem-btn" @click="remUserPass()" :class="rem.userPass?'active':''">
              <el-tooltip class="item" effect="dark" content="记住用户名 和 密码" :open-delay="1000" placement="right">
                <i class="fa fa-check"></i>
              </el-tooltip>
            </div>
            <div class="assist-btn forget-btn" @click="msgStore('userpassHelp')">
              <el-tooltip class="item" effect="dark" content="忘记密码了？" :open-delay="1000" placement="right">
                <i class="fa fa-question"></i>
              </el-tooltip>
            </div>
          </li>
          <li class="submit-wrap">
            <button class="btn" @click="userpassLogin()">登录</button>
          </li>
        </ul>
      </div>
      <div class="tabpanel" :class="tabActive=='phone'?'active':''">
        <ul class="input-wrap">
          <li class="user-wrap">
            <i class="input-icon fal fa-mobile"></i>
            <div class="assist-btn rem-btn" @click="remPhone()" :class="rem.phone?'active':''">
              <el-tooltip class="item" effect="dark" content="记住手机号" :open-delay="1000" placement="right">
                <i class="fa fa-check"></i>
              </el-tooltip>
            </div>
            <input type="text" placeholder="请输入手机号" v-model="inputs.phone"/>
                    </li>
          <li class="pass-wrap">
            <input type="text" placeholder="请输入验证码" @keyup.enter="phoneLogin()" v-model="inputs.code"/>
            <i class="input-icon fal fa-lock"></i>
            <div class="assist-btn sendcode-btn" @click="countDown()" v-show="count==10">发送验证码</div>
            <div class="assist-btn count-down" v-if="count!=10">{{count}}</div>
            <div class="assist-btn forget-btn" @click="msgStore('phoneHelp')">
              <el-tooltip class="item" effect="dark" content="收不到验证码？" :open-delay="1000" placement="right">
                <i class="fa fa-question"></i>
              </el-tooltip>
            </div>
          </li>
          <li class="submit-wrap">
            <button class="btn" @click="phoneLogin()">登录</button>
          </li>
        </ul>
      </div>
      <div class="tabpanel" :class="tabActive=='qrcode'?'active':''">
        <div class="qrcode-wrapper">
          <img :src="login.qrcode" class="qrcode-img" alt="" />
          <p>微信扫描二维码进行登录</p>
        </div>
      </div>
      <div class="tabpanel certificate" :class="tabActive=='app'?'active':''">
        <div class="app-wrapper">
          <ul class="app-list">
            <li v-for="(item,index) in login.app" :key="index">
              <img :src="item.qrcode" alt="" />
              <a :href="item.link" target="_blank">{{item.name}}</a>
              <p>{{item.desc}}</p>
            </li>
          </ul>
          <p class="desc">扫码或者点击按钮下载APP</p>
        </div>
      </div>
    </div>
  </div>
  <ul class="login-tabs" role="tablist">
    <li v-for="(item,index) in tabs" :class="tabActive==item.act?'active':''" @click="tabClik(item.act)" :key="index">
      <el-tooltip class="item" effect="dark" :content="item.name" :open-delay="1000" placement="top">
        <i :class="item.icon"></i><span class="desc">{{item.name}}</span>
      </el-tooltip>
    </li>
  </ul>
  <div class="copyright">© 东华软件股份公司 aYin</div>
</div>
</template>

<script>
import md5 from 'js-md5';
import Logo from "cps/common/header/logo.vue";
import "static/lib/jquery.particleground.min.js"
export default {
  components: {
    "v-logo": Logo
  },
  data() {
    return {
      tabActive: "user",
      tabs: [{
          name: "账号登录",
          icon: "fal fa-user",
          act: "user"
        },
        {
          name: "短信登录",
          icon: "fal fa-mobile",
          act: "phone"
        },
        {
          name: "扫码登录",
          icon: "fal fa-qrcode",
          act: "qrcode"
        },
        {
          name: "APP下载",
          icon: "fal fa-th-large",
          act: "app"
        },
      ],
      count: 10,
      login: "",
      browser: "",
      rem: {
        user: false,
        userPass: false,
        phone: false
      },
      inputs: {
        user: "",
        pass: "",
        phone: "",
        code: ""
      }
    }
  },
  computed: {
    
  },
  methods: {
    userpassLogin() {//用户名密码登录验证！！！！！
      	var url = this.$urls.login;
      	if (this.inputs.user != "" && this.inputs.pass != "") {
			this.$http.get(url,{//有时间试试post登录！！
        params: {
          userid: this.inputs.user,
          userpassword: md5(this.inputs.pass).toUpperCase()
        }
			}).then(response => {
				var data=JSON.parse(response.bodyText);
				//console.log(data.success);
				if(data.success){
					this.msgStore("userpassSuccess");
          //this.$vuex.state.login=data.root;
          this.$session.set("userinfo",data.root);
					this.$session.set('userid',this.inputs.user)//过期时间为秒，此处设置为1小时过期
          //console.log(data);          

          //this.$cookie.set("userInfo",data.root,60*60);
					//this.$cookie.set('userid',this.inputs.user,60*60)//过期时间为秒，此处设置为1小时过期
					setTimeout(function(){
						this.$router.push({path: '/'});
					}.bind(this),1000)
				}else{
					this.msgStore("userpassError");
				}

			}, err => {
				this.msgStore("serverError");
			})
      	} else if (this.inputs.user == "") {
        	this.msgStore("userNone");
      	} else if (this.inputs.pass == "") {
        	this.msgStore("passNone");
      	}
    },
    phoneLogin() {
      if (this.inputs.phone != "" && this.inputs.code != "") {
        //console.log(this.verPhone(this.inputs.phone));
        if (this.verifyPhone(this.inputs.phone)) {
          this.$router.push({
            path: '/'
          });
        } else {
          this.msgStore("phoneNumError");
        }
      } else if (this.inputs.phone == "") {
        this.msgStore("phoneNone");
      } else if (this.inputs.code == "") {
        this.msgStore("codeNone");
      }
    },
    verifyPhone(phone) {
      if (!(/^1[345789]\d{9}$/.test(phone))) {
        //console.log("电话号码格式错误");
        return false;
      } else {
        //console.log("电话号码格式正确");
        return true;
      }
    },
    remUser() {
      if (this.rem.user == false) {
        if (this.inputs.user != "") {
          this.$storage.set("user", this.inputs.user)
          this.rem.user = true;
          this.msgStore("userSave");
        } else {
          this.msgStore("userNone");
        }
      } else {
        this.$storage.del("user");
        this.$storage.del("pass");
        this.rem.user = false;
        this.rem.userPass = false;
        this.msgStore("userDel");
      }
    },
    remUserPass() {
      if (this.rem.userPass == false) {
        if (this.inputs.user != "" && this.inputs.pass != "") {
          this.$storage.set("user", this.inputs.user)
          this.rem.user = true;

          this.$storage.set("pass", this.inputs.pass)
          this.rem.userPass = true;
          this.msgStore("userpassSave");
        } else if (this.inputs.user == "") {
          this.msgStore("userNone");
        } else if (this.inputs.pass == "") {
          this.msgStore("passNone");
        }
      } else {
        this.$storage.del("user")
        this.$storage.del("pass")
        this.rem.user = false;
        this.rem.userPass = false;
        this.msgStore("userpassDel");
      }
    },
    remPhone() {
      if (this.rem.phone == false) {
        if (this.inputs.phone != "") {
          if (this.verifyPhone(this.inputs.phone)) {
            this.$storage.set("phone", this.inputs.phone)
            this.rem.phone = true;
            //console.log(this.$storage.get("phone"));
            this.msgStore("phoneSave");
          } else {
            this.msgStore("phoneNumError");
          }
        } else {
          this.msgStore("phoneNone");
        }
      } else {
        this.$storage.del("phone")
        this.rem.phone = false;
        this.msgStore("phoneDel");
      }
    },
    tabClik(obj) {
      this.tabActive = obj;
    },
    countDown() {
      if (this.inputs.phone != "") {
        if (this.verifyPhone(this.inputs.phone)) {
          var timer = setInterval(() => {
            if (this.count > 0) {
              this.count--;
            } else {
              clearInterval(timer);
              this.count = 10;
            }
            console.log(this.count);
          }, 1000)
          this.msgStore("phoneInfo");
        } else {
          this.msgStore("phoneNumError");
        }
      } else {
        this.msgStore("phoneNone");
      }
    },
    getCode() {
      const TIME_COUNT = 60;
      if (!this.timer) {
        this.count = TIME_COUNT;
        this.show = false;
        this.timer = setInterval(() => {
          if (this.count > 0 && this.count <= TIME_COUNT) {
            this.count--;
          } else {
            this.show = true;
            clearInterval(this.timer);
            this.timer = null;
          }
        }, 1000)
      }
    },
    getData() {
      var url = this.$urls.loginResource;
      this.$http.get(url).then(response => {
        var data = JSON.parse(response.bodyText);
        this.login = data.login;
        this.browser = data.browser;
        //console.log(this.login,this.browser);
      }, err => {

      })
    },
    msgStore(val) {
      if (val == "userNone") {
        this.showMessage("<b>未输入</b>用户名", "warning")
      } else
      if (val == "userSave") {
        this.showMessage("已<b>保存</b>用户名", "info")
      } else
      if (val == "userDel") {
        this.showMessage("已<b>清除</b>保存的用户名", "info")
      } else
      if (val == "passNone") {
        this.showMessage("<b>未输入</b>密码", "warning")
      } else
      if (val == "userpassSave") {
        this.showMessage("已<b>保存</b>用户名和密码", "info")
      } else
      if (val == "userpassDel") {
        this.showMessage("已<b>清除</b>保存的用户名和密码", "info")
      } else
      if (val == "userpassInfo") {
        this.showMessage("已从本地信息恢复用户名和密码", "info")
      } else
      if (val == "userpassError") {
        this.showMessage("用户名和密码不匹配", "error")
      } else
      if (val == "userpassSuccess") {
        this.showMessage("登录成功，即将跳转", "info")
      } else
      if (val == "userpassHelp") {
        this.showMessage('忘记密码了？请联系管理员 18600880088', 'info', 5000)
      } else
      if (val == "phoneNone") {
        this.showMessage("<b>未输入</b>手机号码", "warning")
      } else
      if (val == "phoneSave") {
        this.showMessage("已<b>保存</b>手机号码", "info")
      } else
      if (val == "phoneDel") {
        this.showMessage("已<b>清除</b>保存的手机号码", "info")
      } else
      if (val == "phoneInfo") {
        this.showMessage("验证码发送成功，请注意查收短信", "info")
      } else
      if (val == "phoneError") {
        this.showMessage("手机号或验证码错误！", "error")
      } else
      if (val == "phoneNumError") {
        this.showMessage("手机号输入错误！", "error")
      } else
      if (val == "phoneHelp") {
        this.showMessage('收不到验证码？请联系管理员 18600880088', 'info', 5000)
      } else
      if (val == "codeNone") {
        this.showMessage('<b>未输入</b>验证码', 'warning')
      } else
      if (val == "serverError") {
        this.showMessage('<b>服务器</b>未响应', 'error')
      }
    },
    showMessage(msg, cls, ms) {
      !ms ? ms = 1000 : '';
      this.$message({
        dangerouslyUseHTMLString: true,
        message: msg,
        showClose: true,
        center: true,
        duration: ms,
        type: 'login login-' + cls //success/warning/info/error
      });
    },
    getStorageInfo() {
      if (this.$storage.get("user") != null) {
        this.inputs.user = this.$storage.get("user");
        this.rem.user = true;
      }
      if (this.$storage.get("phone") != null) {
        this.inputs.phone = this.$storage.get("phone");
        this.rem.phone = true;
      }
      if (this.$storage.get("pass") != null) {
        this.inputs.pass = this.$storage.get("pass");
        this.rem.userPass = true;
        //console.log(this.$storage.get("pass"));
        this.msgStore("userpassInfo");
      }
    }
  },
  mounted() {
    this.getData();
    this.getStorageInfo();
    this.$refs.inputUser.focus();
    //console.log(this.$refs.inputUser);
    particleground(this.$refs.particles, {
      dotColor: 'rgba(255,255,255,.1)',
      lineColor: 'rgba(255,255,255,.05)'
    });
  },
}
</script>

<style lang="less">
//@import (reference) "~assets/less/common/ayincolor.less";
//@import (reference) "~assets/less/common/mixins.less";
//@import (reference) "~assets/less/common/animate.less";
</style>



