<template>
  <ul class="menu-inner">
    <template v-for="(item,index) in nav">
	  <el-menu-item name="el-menu-item" v-if="!item.children" :key="item.id" :index="item.url?item.url:''">
        <i :class="item.icon"></i>
        <span slot="title">{{item.label}}</span>
      </el-menu-item>
      <!-- 此菜单下还有子菜单 -->
      <el-submenu name="el-submenu" v-if="item.children" :key="item.id" :index="item.url?item.url:''" :class="$route.params.sub==index?'is-active':''">
        <template slot="title">
          <i :class="item.icon"></i>
          <span slot="title">{{item.label}}</span>
        </template>
        <!-- 递归 -->
        <navMenu :nav="item.children"></navMenu>
      </el-submenu>
    </template>
  </ul>
</template>

<script>
  export default {
    name: 'NavMenu',
    props: ['nav'],
    data() {
      return {
      }
    },
    methods: {
	    	handleOpen(key, keyPath) {
		    console.log(key, keyPath);
		},
		handleClose(key, keyPath) {
		    console.log(key, keyPath);
		}
    },
    mounted(){
    		//this.$vuex.commit('clsTreeState');//链接到tree的功能，本主题加载的时候清除treeLastClick
    }
  }
</script>

<style scoped>

