<template>
  <el-row class="portlet-overview">
	  <el-col :span="6" v-for="(item,index) in overview" :key="index">
	  	<el-card class="portlet-card portlet-card-ov">
			<el-tooltip class="item" effect="dark" :open-delay="1000" :content="item.company+' '+item.project" placement="top">
				<p class="title">
					{{item.company}} <span class="sub">{{item.project}}</span>
				</p>
			</el-tooltip>
		  <p class="info">
		  	<el-tooltip class="item" effect="dark" :open-delay="1000" content="起始-截止 日期" placement="right">
		  		<span class="date"><i class="fa fa-calendar-alt"></i> {{item.date}}</span>
		  	</el-tooltip>
		  	<el-tooltip class="item" effect="dark" :open-delay="1000" content="参与人数" placement="right">
		      <span class="people"><i class="fa fa-users"></i> {{item.people}}</span>
		    </el-tooltip>
		  </p>
		  <el-tooltip class="item" effect="dark" :open-delay="1000" content="项目进度" placement="right">
		  <el-progress :percentage="item.achieved" :show-text="false"></el-progress>
		  </el-tooltip>
		</el-card>
	  </el-col>
	</el-row>
</template>

<script>
export default {
  data () {
    return {
      overview:[]
    }
  },
  methods:{
  	getData(){
		var url="static/data/portlets.json";//模拟从服务器读取数据！
		this.$http.get(url).then(response=>{
         	var data= JSON.parse(response.bodyText);
         	this.overview=data.portlets.overview;
         	//console.log(data);
		},err=>{
			
		})
	}
  },
  mounted(){
  	this.getData();
  }
}
</script>

<style lang="less">

</style>



