<template>
<div class="content-wrapper">
  <el-breadcrumb separator="/">
    <el-breadcrumb-item :to="{ path: '/' }">首页</el-breadcrumb-item>
    <el-breadcrumb-item>{{$route.params.sub}}</el-breadcrumb-item>
    <el-breadcrumb-item>{{$route.params.id}}</el-breadcrumb-item>
  </el-breadcrumb>
  
  <el-form ref="project" class="project-form project-form-edit" :model="project" label-width="90px" style="width:825px;">
    <el-row>
      <el-col :span="24">
        <el-form-item label="项目名称">
          <el-input v-model="project.title"></el-input>
        </el-form-item>
      </el-col>
    </el-row>
    
    <el-row>
      <el-col :span="17">
        <el-form-item label="客户名称">
          <el-input v-model="project.clientName"></el-input>
        </el-form-item>
      </el-col>
      <el-col :span="7">
        <el-form-item label="项目金额">
          <el-input v-model="project.itemAmount"></el-input>
        </el-form-item>
      </el-col>
      
    </el-row>
    <el-row>
      <el-col :span="10">
        <el-form-item label="MIS号">
          <el-input v-model="project.MIS"></el-input>
        </el-form-item>
      </el-col>
      <el-col :span="7">
        <el-form-item label="销售">
          <el-input v-model="project.seller"></el-input>
        </el-form-item>
      </el-col>
      <el-col :span="7">
        <el-form-item label="商务">
          <el-input v-model="project.salesman"></el-input>
        </el-form-item>
      </el-col>
    </el-row>

    
    <el-row>
      <el-col :span="10">
        <el-form-item label="起止时间">
          <el-date-picker style="width:100%;" v-model="project.SEdate" type="daterange" align="right" unlink-panels range-separator="-" start-placeholder="开始日期" end-placeholder="结束日期" :picker-options="pickerOptions"></el-date-picker>
        </el-form-item>
      </el-col>
      <el-col :span="14">
        <el-form-item label="中标单位">
          <el-radio-group v-model="project.succBidder">
            <el-radio-button v-for="item in project.succBidderList" :label="item" :key="item">{{item}}</el-radio-button>
          </el-radio-group>
          
        </el-form-item>
      </el-col>
    </el-row>
    <el-row>
      <el-col :span="24">
        <el-form-item label="类型">
          <el-checkbox-group v-model="project.type">
            <el-checkbox-button v-for="item in project.typeList" :label="item" :key="item">{{item}}</el-checkbox-button>
          </el-checkbox-group>
        </el-form-item>
      </el-col>
    </el-row>
    <el-row>
      <el-col :span="24">
        <el-form-item label="阶段">
          <el-radio-group v-model="project.stage">
            <el-radio-button v-for="item in project.stageList" :label="item" :key="item">{{item}}</el-radio-button>
          </el-radio-group>
        </el-form-item>
      </el-col>
    </el-row>
    <el-row>
      <el-col :span="24">
        <el-form-item label="备注">
          <el-input type="textarea" v-model="project.desc" rows="5"></el-input>
        </el-form-item>
      </el-col>
    </el-row>
    
    <el-form-item>
      <el-button type="primary" @click="onSubmit">立即创建</el-button>
      <el-button>取消</el-button>
    </el-form-item>
  </el-form>


</div>
</template>

<script>
export default {
  data() {
    return {
      pickerOptions: {
        shortcuts: [{
          text: '最近一周',
          onClick(picker) {
            const end = new Date();
            const start = new Date();
            start.setTime(start.getTime() - 3600 * 1000 * 24 * 7);
            picker.$emit('pick', [start, end]);
          }
        }, {
          text: '最近一个月',
          onClick(picker) {
            const end = new Date();
            const start = new Date();
            start.setTime(start.getTime() - 3600 * 1000 * 24 * 30);
            picker.$emit('pick', [start, end]);
          }
        }, {
          text: '最近三个月',
          onClick(picker) {
            const end = new Date();
            const start = new Date();
            start.setTime(start.getTime() - 3600 * 1000 * 24 * 90);
            picker.$emit('pick', [start, end]);
          }
        }]
      },
      project: {
        title: "",
        type:[],
        stage:"",
        succBidder:"",
        MIS: "",
        itemAmount: "",
        clientName: "",
        seller: "",
        salesman: "",
        SEdate:"",
        desc: "",
        succBidderList:["东华软件","合创科技","智慧城市"],
        typeList:["硬件","软件","集成","运维","设计","外包","服务","设备","信息化"],
        stageList:["前期","提前","实施","验收","结束","质保"]
      }
    };
  },
  watch: {},
  methods: {
    onSubmit() {
      console.log("submit!");
      
    }
  },
  mounted() {}
};
</script>

<style lang="less">
</style>



