<template>
<el-dialog :title="dialogTitle" :visible.sync="dialog.show" :before-close="closeDialog">
  <v-form :is="dialog.form"></v-form>
  <div slot="footer" class="dialog-footer">
    <el-button  @click="closeDialog()">取 消</el-button>
    <el-button type="primary" @click="closeDialog()">确 定</el-button>
  </div>
</el-dialog>
</template>

<script>
import NewTask from "./newTask.vue";
import NewDoc from "./newDoc.vue";
export default {
  props:["dialog"],
  components:{
    "NewTask":NewTask,
    "NewDoc":NewDoc
  },
  computed:{
    dialogTitle:function(){
      var judge=this.dialog.form,type="";
      if(judge=="NewTask"){
        type="发布任务: "
      }else if(judge="NewDoc"){
        type="上传文档: "
      }
      return type+this.dialog.content.title;
    }
  },
  data() {
    return {
      
    };
  },
  watch: {
    
  },
  methods: {
    closeDialog(){
      this.$emit('dialogClose',false);
    }
    
  },
  mounted() {
    
  }
};
</script>


