<template>
  <el-row>
    <el-col :span="12" v-for="(item,index) in groupBox" :key="'groupBox'+index">
      <el-card shadow="hover" class="portlet-card portlet-cardMini">
        <div class="mini-info">
          <span class="desc">{{item.name}}</span>
          <p>
            <i :class="item.icon"></i>
            <span class="count">{{item.count}} <span class="badge">{{item.badge}}</span></span>
          </p>
        </div>
      </el-card>
    </el-col>
  </el-row>
</template>

<script>
export default {
  name: 'app',
  data () {
    return {
      groupBox:[
        {name:"项目",icon:"fal fa-cubes",count:20,badge:"+2"},
        {name:"任务",icon:"fal fa-tasks",count:329},
        {name:"文档",icon:"fal fa-books",count:532,badge:"+23"},
        {name:"通知",icon:"fal fa-comments",count:26}
      ]
    }
  }
}
</script>

<style lang="less">

</style>



