<template>
<el-form ref="task" class="dialog-form dialog-form-edit" :model="newtask" label-width="90px" style="width:100%;">
    <el-row>
      <el-col :span="24">
        <el-form-item label="任务名称">
          <el-input v-model="newtask.title"></el-input>
        </el-form-item>
      </el-col>
    </el-row>
    
    <el-row>
      <el-col :span="8">
        <el-form-item label="发布时间">
          <el-input v-model="newtask.startDate"></el-input>
        </el-form-item>
      </el-col>
      <el-col :span="8">
        <el-form-item label="发布人">
          <el-input v-model="newtask.publisher"></el-input>
        </el-form-item>
      </el-col>
      <el-col :span="8">
        <el-form-item label="接收人">
          <el-input v-model="newtask.excepter"></el-input>
        </el-form-item>
      </el-col>
      
    </el-row>


    <el-row>
      
      <el-col :span="8">
        <el-form-item label="完成时间">
          <el-input v-model="newtask.endData"></el-input>
        </el-form-item>
      </el-col>
      <el-col :span="8">
        <el-form-item label="任务类型">
          <el-input v-model="newtask.type"></el-input>
        </el-form-item>
      </el-col>
      <el-col :span="8">
        <el-form-item label="任务级别">
          <el-input v-model="newtask.level"></el-input>
        </el-form-item>
      </el-col>
    </el-row>
    <el-row>
      <el-col :span="24">
        <el-form-item label="任务详情">
          <el-input type="textarea" v-model="newtask.details" rows="5"></el-input>
        </el-form-item>
      </el-col>
    </el-row>
    <el-row>
      <el-col :span="24">
        <el-form-item label="备注">
          <el-input type="textarea" v-model="newtask.desc" rows="2"></el-input>
        </el-form-item>
      </el-col>
    </el-row>
  </el-form>
</template>

<script>
export default {
  props: [],
  data() {
    return {
      formLabelWidth: '120px',
      newtask: {
        title: '',
        publisher: '',
        excepter: '',
        startDate: '',
        endData: '',
        type: '',
        level: '',
        type: '',
        details: '',
        desc: ''
      }
    };
  },
  watch: {

  },
  methods: {

  },
  mounted() {

  }
};
</script>



