<template>
	<el-menu  class="el-menu-wrapper" ref="mainMenu" mode="horizontal" @select="handleSelect" :router="true" menu-trigger="click"><!--:default-active="$route.path"-->
	  <el-menu-item v-for="(item,index,key) in $vuex.state.header.nav" :class="$route.params.sub==index||$route.path==item.url?'is-active':''" v-if="key<navLength" :index="item.url?item.url:''" :key="item.index" :disabled="item.dis">
	  		<i :class="item.icon"></i> {{item.label}} 
	  </el-menu-item>
	  <el-submenu index="" ref="subMenu" id="subMenu" :class="subMenuActive?'is-active':''"><!--:class="$route.params.sub==index||$route.path==item.url?'is-active':''"-->
	    <template slot="title"><i class="fa fa-th"></i> 更多</template><!--:data-active="$route.params.sub==index||$route.path==item.url?'is-active':''"-->
	    <el-menu-item v-for="(item,index,key) in $vuex.state.header.nav"  :class="$route.params.sub==index||$route.path==item.url?'is-active':''"  v-if="key>navLength-1" :index="item.url?item.url:''" :key="item.index" :disabled="item.dis">
	    		<i :class="item.icon"></i> {{item.label}}
	    </el-menu-item>
	  </el-submenu>
	</el-menu>
</template>

<script>
export default {
  props: ['clientWidth'],
  data () {
    return {
    		subMenuActive:false
    }
  },
  computed: {
    navLength: function () {
    	  if(this.clientWidth>0){
    	  	return Math.floor((this.clientWidth-200)/100/2-1);
    	  }else{
    	  	return 10;
    	  }
    }
  },
  methods:{
  	handleSelect(key, keyPath,vnode){//导航触发的选择事件
  		this.$vuex.commit('clsTreeState');//链接到tree的功能，导航点击的时候清除treeLastClick
  		//console.log(key,keyPath,node);
  		//console.log(vnode.$parent.$vnode.data.ref=="mainMenu")
  		if(vnode.$parent.$vnode.data.ref=="mainMenu"){//判断父元素是否为“mainMeun”然后改变下拉菜单的激活状态
  			this.subMenuActive=false;
  		}
    }
  },
  mounted(){
  	this.$timer.set("getSubActive",()=>{//本段代码，循环子菜单，检测是否包含激活的菜单，然后给subMenu加is-active状态。
  		var subMenu=this.$refs.subMenu.$children;
  		for(var i=0;i<subMenu.length;i++){
  			if(subMenu[i].$vnode.data.class=="is-active"){
  				this.subMenuActive=true
  			}
  			//console.log(subMenu[i].$vnode.data.class);
  			//console.log(subMenu[i].className);
  		}
  		//console.log(this.$refs.subMenu.$children.length);
  	},1000)
  	//$el: li.el-menu-item.is-active
  	//this.$handleClass.add(document.getElementById("subMenu"),"is-active");
  	//console.log(this.$refs.subMenu)
  	//this.handleClass.del(document.getElementById("html"),state.theme.lastLayout);
  	
  }
}
</script>



