<template>
  	<div class="nav-wrapper" :class="navTotalPage>1?'nav-multi-page':''" :style="'left:'+((this.clientWidth-80-560)/2+90)+'px'">
  		<i class="nav-ctrl nav-ctrl-left fal fa-arrow-circle-left" :class="navCurrentPage==0?'nav-ctrl-disabled':''" v-if="navTotalPage>1" @click="navScroll('left')"></i>
  		<i class="nav-ctrl nav-ctrl-right fal fa-arrow-circle-right" :class="(navCurrentPage+1)==navTotalPage?'nav-ctrl-disabled':''" v-if="navTotalPage>1" @click="navScroll('right')"></i>
  		<div class="scroll-wrapper" :style="'width:'+navWrapWidth+'px'" v-if="clientWidth>0">
	  		<div class="scroll-inner" :style="'width:'+navTotalWidth+'px;left:'+-navCurrentPage*navWrapWidth+'px'">
	  			<ul class="nav-list" ref="navList"><!--@click链接到tree的功能，点击主导航清除treeLastClick vuex和每个主题导航中都有这个功能-->
			  		<li v-for="(item,index) in $vuex.state.header.nav" @click="$vuex.commit('clsTreeState')" :key="index" :class="$route.params.sub==index||$route.path==item.url?'active':''">
			  			<router-link  class="nav-item" :to="item.url">
			  				<i :class="item.icon"></i>
			  				<p class="desc">{{item.label}}</p>
			  			</router-link>
			  		</li>
			  	</ul>
	  		</div>
	  	</div>
	  	<ul class="nav-pointer" :class="navTotalPage>3?'nav-pointer-2row':''" v-if="navTotalPage>1">
	  		<li v-for="(item,index) in navTotalPage" :class="navCurrentPage==index?'active':''" @mouseenter="navPointer(index,'enter')"  @mouseleave="navPointer(index,'leave')" :key="index">{{index}}</li>
	  	</ul>
  	</div>
</template>

<script>
  export default {
    name: 'NavMenu',
    data() {
      return {
      	clientWidth:"",
      	navCurrentPage:0
      }
    },
    watch: {
      '$route'(to, from) {
      	//console.log(this.$route.params.sub,this.$route.path)
	  }
    },
    computed: {
    		navLength:function(){
    			return Object.keys(this.$vuex.state.header.nav).length;	
    		},
	    navItemLength: function () {
	    	  var count=Math.floor((this.clientWidth-80-560-100)/90-1);
	    	  if(count>this.navLength){
	    	  	return this.navLength;
	    	  }else{
	    	  	count<1?count=1:'';//count不能小于1否则后续计算会出错！
						//console.log(count);
	    	  	return count;
	    	  }
	      //计算导航的数量，body宽度-logo宽度80-右侧辅助功能550，除以导航宽度90
	    },
	    navWrapWidth:function(){
	    		return 90*this.navItemLength;
	    },
	    navTotalPage:function(){
	    		var mathR=Math.ceil(90*this.navLength/this.navWrapWidth);
	    		if(mathR>=0){
						//console.log('navTotalPage'+Math.ceil(90*this.navLength/this.navWrapWidth));
						return Math.ceil(90*this.navLength/this.navWrapWidth);
	    		}else{
	    			return 0;
	    		}
	    },
	    navTotalWidth:function(){
	    		return this.navTotalPage*this.navWrapWidth;
	    }
	},
    methods: {
	    navScroll(dir){
	    		if(dir=="left"&&this.navCurrentPage>0){
	    			--this.navCurrentPage;
	    		}else if(dir=="right"&&this.navCurrentPage<this.navTotalPage-1){
	    			++this.navCurrentPage;
	    		}
	    		//console.log(this.navCurrentPage+"navTotalPage："+this.navTotalPage);
	    },
	    navPointer(index,act){
	    		if(act=="enter"){
	    			this.$timer.set("navPointer",()=>{
	    				this.navCurrentPage=index;
	    			},500)
	    		}else if(act=="leave"){
	    			this.$timer.clear("navPointer")
	    		}
	    		//console.log(this.navCurrentPage,this.navTotalPage)
	    },
	    locateCurrentNav(){
	    		this.$timer.set("countNavCurrentPage",()=>{
		    		if(this.$refs.navList){
		    			var nl=this.$refs.navList.children;
			    		//console.log(nl);
			    		for(var i=0;i<nl.length;i++){
			    			//console.log(nl[i].className);
			    			if(nl[i].className=="active"){
			    				this.navCurrentPage=Math.ceil((i+1)/this.navItemLength)-1
			    				//console.log(Math.ceil(i/this.navItemLength)-1,(i+1),this.navItemLength)
			    			}
			    		}
			    		//console.log("get")
		    		}else{
		    			//console.log("notget")
		    		}
		    },1000);
	    }
    },
    mounted(){
	    	this.clientWidth = document.body.clientWidth;
	    	//console.log(this.clientWidth)
	    	this.locateCurrentNav();
	    window.onresize =()=>{
	    		this.$timer.set("windowWidth",()=>{
	    			this.clientWidth = document.body.clientWidth;
	    			this.locateCurrentNav();
	    		},500)
	    };
  	}
  }
</script>
<style scoped>
</style>


