<template>
<el-card shadow="hover" class="portlet-card">
    <div slot="header" class="clearfix">
      <span class="title">项目统计</span>
      <el-button class="ctrl-btn" type="text">查看列表 <i class="fal fa-angle-double-right"></i></el-button>
    </div>
    <div id="chart-pie01" style="height:280px;"></div>
</el-card>
</template>

<script>

//import echarts from 'echarts';
import echarts from "static/lib/echarts.min.js"
import $c from "static/lib/ayincolor-vue.js"
 
export default {
  data() {
    return {}
  },
  methods: {},
  mounted() {
		//this.$timer.set("test",()=>{
		//},3000)
		//console.log($c.rel5);
    
        var echartPie01 = echarts.init(document.getElementById('chart-pie01'));
        var optionPie01 = {
            tooltip: {
                trigger: 'item',
                formatter: "{a} <br/>{b}: {c} ({d}%)"
            },
            legend: {
            // orient: 'vertical',
            show:true,
                x: 'center',
                y:'center',
                width:"100px",
                textStyle: {
                color: $c.gyd5
                },
                data:['直接访问','邮件营销','联盟广告','视频广告','搜索引擎']
            },
            series: [
                {
                    name:'数据类别',
                    type:'pie',
                    radius: ['60%', '90%'],
                    center: ['50%', '50%'],
                    avoidLabelOverlap: false,
                    label: {
                        normal: {
                            show: false,
                            position: 'outside'
                        },
                        emphasis: {
                            show: false,
                            textStyle: {
                                fontSize: '20',
                                color:$c.gyd5
                            }
                        }
                    },
                    labelLine: {
                        normal: {
                            show: false
                        }
                    },
                    data:[
                        {value:335, name:'直接访问',itemStyle: {normal:{color:$c.orhl5}}},
                        {value:310, name:'邮件营销',itemStyle: {normal:{color:$c.orhl4}}},
                        {value:234, name:'联盟广告',itemStyle: {normal:{color:$c.ychl3}}},
                        {value:135, name:'视频广告',itemStyle: {normal:{color:$c.chhl5}}},
                        {value:1548, name:'搜索引擎',itemStyle: {normal:{color:$c.chhl4}}}

                       
                    ]
                }
            ]
        };
		
		this.$timer.set("echartPie01",()=>{
			echartPie01.setOption(optionPie01);
    	    echartPie01.resize();	
		},1000)



  }
}
</script>

<style lang="less">

</style>



