<template>
  	<el-container class="el-container-top layout-enlighten" :class="$vuex.state.switch.maximize?'maximize':''">
	  <v-restore></v-restore>
	  <el-aside :class="$vuex.state.switch.asideIsCollapse?'el-aside-shrink':'el-aside-enlarge'">
	  	<v-aside></v-aside>
	  </el-aside>
	  <section class="el-container is-vertical">
	  	<header class="el-header"><v-header></v-header></header>
	  	<main class="el-main"><router-view></router-view></main>
	  </section>
	</el-container>
</template>

<script>
import Header from "./header.vue";
import Aside from "./aside/index-aside.vue";
import RestoreBtn from "cps/common/header/restore-btn.vue";
//import Footer from "./footer.vue";
export default {
  name: 'app',
  data () {
    return {
    }
  },
  watch:{
	  isCollapse(curVal,oldVal) {
	    console.log(curVal);
	    //this.visible = val;
	  }
  },
  components:{
  	"v-header":Header,
  	"v-aside":Aside,
  	"v-restore":RestoreBtn
  	//"v-footer":Footer
  },
  mounted(){}
}
</script>

<style lang="less" scoped="scoped"></style>



