


<template>
<el-card shadow="hover" class="portlet-card">
  <div slot="header" class="clearfix">
    <span class="title">项目统计</span>
    <el-button class="ctrl-btn" type="text">查看列表 <i class="fal fa-angle-double-right"></i></el-button>
  </div>
  <div id="tabs-video-01" style="height:280px;"></div>
</el-card>
</template>

<script>

//import echarts from 'echarts';
import echarts from "static/lib/echarts.min.js"
import $c from "static/lib/ayincolor-vue.js"
 
export default {
  data() {
    return {}
  },
  methods: {},
  mounted() {
		//this.$timer.set("test",()=>{
		//},3000)
		//console.log($c.rel5);
		var echart2 = echarts.init(document.getElementById('tabs-video-01'));
    var option2 = {
      tooltip: {
        trigger: 'axis',
        axisPointer: { // 坐标轴指示器，坐标轴触发有效
          type: 'shadow' // 默认为直线，可选为：'line' | 'shadow'
        }
      },
      legend: {
        //show:false,
        data: ['本地视频会议', '异地视频会议'],
        top:"35",
        textStyle: {
          color: $c.gyd5
        }
      },
      toolbox: {
        //y: 'top',
        //top:0,
        feature: {
          magicType: {
            type: ['line', 'bar']
          }
        }
      },
      grid: {
        left: '2%',
        right: '2%',
        bottom: '2%',
        top: "25%",
        containLabel: true
      },
      yAxis: {
        type: 'value',
        axisLine: {
          lineStyle: {
            color: $c.gyd5
          },
        },
        splitLine: {
          lineStyle: {
            color: $c.gyl2
          },
        },
        axisLabel: {
          textStyle: {
            color: $c.gyd5,
            align: 'right'
          }
        }
      },
      xAxis: {
        type: 'category',
        data: ['2016 十二月', '一月', '二月', '三月', '四月', '五月', '六月', '七月', '八月', '九月', '十月', '十一月'],
        axisLine: {
          lineStyle: {
            color: $c.gyd5
          },
        },
        splitLine: {
          lineStyle: {
            color: $c.gyl2
          },
        },
        axisLabel: {
          textStyle: {
            color: $c.gyd5,
            align: 'center'
          }
        }
      },
      series: [{
          name: '本地视频会议',
          type: 'bar',
          stack: '01',
          label: {
            normal: {
              show: false,
              position: 'insideRight'
            }
          },
          itemStyle: {
            normal: {
              color: $c.bll4,
              barBorderRadius: 2
            }
          },
          data: [235, 210, 187, 212, 278, 220, 320, 302, 301, 334, 390, 330]
        },
        {
          name: '异地视频会议',
          type: 'bar',
          stack: '02',
          label: {
            normal: {
              show: false,
              position: 'insideRight'
            }
          },
          itemStyle: {
            normal: {
              color: $c.grm4,
              barBorderRadius: 2
            }
          },
          data: [68, 121, 34, 56, 23, 120, 132, 101, 134, 90, 230, 210]
        }
      ]
		};
		
		this.$timer.set("chart01",()=>{
			echart2.setOption(option2);
    	echart2.resize();	
		},1000)



  }
}
</script>

<style lang="less">

</style>



