	        <i class="fal fa-compress-wide" v-if="$vuex.state.switch.maximize"></i>
	   	</el-tooltip>
	</div>
</template>

<script>
export default {
  data () {
    return {
    }
  },
  computed: {
  },
  methods:{
  },
  mounted(){
  }
}
</script>



