<template>
  <div class="subPage">
    <el-container><!--:class="[$route.path=='/login'?'login-logo':'logo-wrapper', $vuex.state.switch.logoHover?'logo-moreInfo':'']" -->
	  <el-aside :width="asideWidth+'px'" :class="asideWidth>199?'aside-show':'aside-hidden'" v-if="$vuex.state.theme.layout!='layoutEnlighten'&&tempTree">
	  	<v-tree></v-tree>
	  </el-aside>
	  <ul class="aside-ctrl" :style="'left:'+asideWidth+'px'" v-if="$vuex.state.theme.layout!='layoutEnlighten'&&tempTree">
  		<li @click="expandAside()" :class="asideWidth>=400||asideWidth<200?'disabled':''"><i class="fa fa-plus"></i></li>
  		<li @click="toogleAside()"><i class="fa" :class="asideWidth>199?'fa-arrow-alt-from-right':'fa-arrow-alt-from-left'"></i></li>
  		<li @click="reduceAside()" :class="asideWidth<=200?'disabled':''"><i class="fa fa-minus"></i></li>
  	  </ul>
	  <el-main>
	  	<router-view></router-view>
	  </el-main>
	</el-container>
  </div>
</template>

<script>
import Tree from "cps/common/sub/tree.vue";
export default {
  name: 'app',
  data () {
    return {
      asideWidth:300,
			paramsSub:"",
			tempTree:""
    }
  },
  components:{
  	"v-tree":Tree,
	},
	watch: {
		'$route'(to, from) {
			this.$timer.set("tempTreeload",()=>{
				this.tempTree=this.$vuex.state.header.nav[this.$route.params.sub].children;
				//console.log(this.tempTree);
			},500)
		}
	},
  methods:{
  	expandAside(){
  		if(this.asideWidth<400&&this.asideWidth>1){
  			this.asideWidth=this.asideWidth+100;
  		}
  	},
  	reduceAside(){
  		if(this.asideWidth>200){
  			this.asideWidth=this.asideWidth-100;
  		}
  	},
  	toogleAside(){
  		if(this.asideWidth>0){
  			this.asideWidth=0;
  		}else{
  			this.asideWidth=300;
  		}
  	}
  },
  mounted(){
		this.$timer.set("tempTreeload",()=>{
			this.tempTree=this.$vuex.state.header.nav[this.$route.params.sub].children;
			//console.log(this.$vuex.state.header.nav[this.$route.params.sub])
		},500)
	}
}
</script>

<style lang="less">
</style>



