<template>
  <div class="aside-wrapper">
    <v-logo></v-logo>
  	<el-menu :default-active="$route.path" @open="handleOpen" @close="handleClose" :collapse="$vuex.state.switch.asideIsCollapse"  :router="true"  >
		<navMenu :nav="nav"></navMenu>
	</el-menu>
  </div>
</template>

<script>
import navMenu from './nav.vue';
import Logo from "cps/common/header/logo.vue";
export default {
  components: {
	  "navMenu": navMenu,
	  "v-logo":Logo
  },
  data () {
    return {
      nav:[]
    }
  },
  watch:{
  },
  methods:{
	getData(){
		var url=this.$urls.header;//模拟从服务器读取数据！
		this.$http.get(url).then(response=>{
         	var data= JSON.parse(response.bodyText);
         	this.nav=data.nav;
		},err=>{
			
		})
	},
	handleOpen(key, keyPath) {
	    //console.log(key, keyPath);
	},
	handleClose(key, keyPath) {
	    //console.log(key, keyPath);
	}
  },
  mounted(){
  		this.getData();
  }
}
</script>
<style lang="less">
</style>



