import Vue from 'vue'
import Router from 'vue-router'
Vue.use(Router)
import vuex from "@/vuex.js";
import {timer,session} from "./model/common.js";

import layout from "cps/layout/index-layout.vue";
import login from "cps/common/login.vue";

import dashboard from "cps/common/dashboard/index-dashboard.vue";
import dashboardDemo from "cps/common/dashboard-demo/index-dashboard-demo.vue";
import dashboardMap from "cps/common/dashboard/index-dashboard-map.vue";
import sub from "cps/common/sub/index-sub.vue";
import welcome from "cps/common/sub/welcome.vue";
import grid from "cps/common/sub/grid.vue";
//import formEdit from "cps/common/sub/form-edit.vue";
//import formView from "cps/common/sub/form-view.vue";
import ayincolor from "cps/demo/ayincolor/ayincolor.vue";

//project	
	import projectGrid from "cps/features/project/grid.vue";
	import projectFormEdit from "cps/features/project/form-edit.vue";
	import projectFormView from "cps/features/project/form-view.vue";

//document
	import documentGrid from "cps/features/document/grid.vue";

const router=new Router({
  //mode:"history",
  routes: [
    {path:'/login',component:login},
    {path:'/',component: layout,children:[
			{path:'/',component:dashboard},
			{path:'/map',component:dashboardMap},
			{path:'/dashboardDemo',component:dashboardDemo},
			{path:'/ayincolor',component:ayincolor},
			{path:'/grid/:id',component:grid},
			{path:'/:sub',component:sub,children:[
				{path:'/:sub',component:welcome},
				{path:'/:sub/:id',component:welcome},
				{path:'/:sub(\project)/grid/:id',component:projectGrid},
				{path:'/:sub(\project)/formEdit/:id',component:projectFormEdit},
				{path:'/:sub(\project)/formView/:id',component:projectFormView},
				{path:'/:sub(\document)/grid/:id',component:documentGrid},
				//{path:'/:sub/grid/:id',component:grid},
				//{path:'/', redirect:'user01'}
			]}
	]},
  ]
})
