<template>
  <div class="header-wrapper">
	<v-nav :clientWidth="clientWidth"></v-nav>
	<v-assist :searchWidth="searchWidth"></v-assist>
  </div>
</template>
<script>
import Assist from "cps/common/header/assist.vue";
import Nav from "./nav.vue";
export default {
  name:'app2',
  components:{
  	"v-assist":Assist,
  	"v-nav":Nav
  },
  data () {
    return {
    		clientWidth:0
    }
  },
  computed:{
  	searchWidth:function(){
  		var mathR=Math.floor((this.clientWidth-200)/2-310);
  		return mathR;
  	}
  },
  methods:{
  	
  },
  mounted(){
	this.clientWidth = document.body.clientWidth;
    window.onresize =()=>{
    		this.$timer.set("windowWidth",()=>{
    			this.clientWidth = document.body.clientWidth;
    		},500)
    };
  }
}
</script>

<style lang="less">
/*@import "~assets/css/common.less";*/
</style>



