<template>
<div class="content-wrapper">
  <el-breadcrumb separator="/">
    <el-breadcrumb-item :to="{ path: '/' }">首页</el-breadcrumb-item>
    <el-breadcrumb-item>{{$route.params.sub}}</el-breadcrumb-item>
    <el-breadcrumb-item>{{$route.params.id}}</el-breadcrumb-item>
  </el-breadcrumb>

  <div class="router-back" @click="routerBack()">
    <i class="fal fa-arrow-square-left"></i> 返回
  </div>

  <el-form ref="project" class="project-form project-form-view" label-width="90px" style="width:900px;">
    <div class="header">
      <h1 class="title">
        <!-- <span class="desc">项目名称:</span>-->
        {{projectInfo.title}}
      </h1>
      <span>客户名称：{{projectInfo.clientName}}</span>
      <div class="ctrl-bar">
        <el-button-group>
          <el-button type="default" icon="fal fa-edit"> 编辑</el-button>
          <el-button type="danger" icon="el-icon-delete">删除</el-button>
        </el-button-group>
      </div>
    </div>

    <el-collapse v-model="activeNames" @change="collapseHandleChange">
      <el-collapse-item name="1" title="项目详情">
        <div class="form-wrapper">
        <el-row>
          <el-col :span="9">
            <el-form-item label="MIS号">
              {{projectInfo.MIS}}
            </el-form-item>
          </el-col>
          <el-col :span="7">
            <el-form-item label="中标单位">
              {{projectInfo.succBidder}}
            </el-form-item>
          </el-col>
          <el-col :span="7">
            <el-form-item label="项目金额">
              {{projectInfo.itemAmount}}
            </el-form-item>
          </el-col>
        </el-row>
        <el-row>
          <el-col :span="9">
            <el-form-item label="起始时间">
              {{projectInfo.PST}}
            </el-form-item>
          </el-col>
          <el-col :span="7">
            <el-form-item label="销售">
              {{projectInfo.seller}}
            </el-form-item>
          </el-col>
          <el-col :span="7">
            <el-form-item label="商务">
              {{projectInfo.salesman}}
            </el-form-item>
          </el-col>

        </el-row>

        <el-row>

          <el-col :span="9">
            <el-form-item label="截止时间">
              {{projectInfo.PET}}
            </el-form-item>
          </el-col>
          <el-col :span="7">
            <el-form-item label="阶段">
              {{projectInfo.stage}}
            </el-form-item>
          </el-col>
          <el-col :span="7">
            <el-form-item label="类型">
              {{projectInfo.type}}
            </el-form-item>
          </el-col>
        </el-row>
        <el-row>
          <el-col :span="24">
            <el-form-item label="备注">
              {{projectInfo.desc}}
            </el-form-item>
          </el-col>
        </el-row>
        </div>
      </el-collapse-item>
      <el-collapse-item title="项目任务" name="2">
        <div class="ctrl-bar">
          <el-button-group>
            <el-button type="default" icon="fal fa-plus"> 新增任务</el-button>
            <el-button type="default" icon="fal fa-square"> 选择</el-button>
          </el-button-group>
        </div>
        <el-table :data="projectInfo.taskList" style="width: 100%">
          <el-table-column prop="title" label="标题" width="200"></el-table-column>
          <el-table-column prop="publisher" label="发布人" width="100"></el-table-column>
          <el-table-column prop="startDate" label="开始时间" width="150"></el-table-column>
          <el-table-column prop="endDate" label="结束时间" width="150"></el-table-column>
          <el-table-column prop="delay" label="是否延期" width="100"></el-table-column>
          <el-table-column prop="type" label="类型" width="80"></el-table-column>
          <el-table-column prop="levle" label="等级" width="80"></el-table-column>
          <el-table-column prop="rate" label="评级" width="80"></el-table-column>
          <el-table-column prop="details" label="详情" width="580"></el-table-column>
          <el-table-column prop="comments" label="备注" width="200"></el-table-column>
        </el-table>
      </el-collapse-item>
      <el-collapse-item title="项目文档" name="3">
        <div class="ctrl-bar">
          <el-button-group>
            <el-button type="default" icon="fal fa-plus"> 添加文档</el-button>
            <el-button type="default" icon="fal fa-square"> 选择</el-button>
          </el-button-group>
        </div>
        <el-table :data="projectInfo.docList" style="width: 100%">
          <el-table-column prop="state" label="阶段" width="150"></el-table-column>
          <el-table-column prop="title" label="标题" width="200"></el-table-column>
          <el-table-column prop="task" label="所属任务"></el-table-column>
        </el-table>
      </el-collapse-item>
    </el-collapse>

  </el-form>

</div>
</template>

<script>
export default {
  data() {
    return {
      projectInfo: "",
      activeNames: ['1']
    };
  },
  watch: {},
  methods: {
    test() {
      console.log("test");
      return;
    },
    routerBack() {
      this.$router.back(-1);
    },
    collapseHandleChange(val) {
      console.log(val);
    },
    getData() {
      var url = this.$urls.project;
      this.$http.get(url).then(
        response => {
          var data = JSON.parse(response.bodyText);
          this.projectInfo = data.project.list[parseInt(this.$route.params.id) - 1]
        },
        error => {}
      );
    }
  },
  mounted() {
    this.getData();
  }
};
</script>

<style lang="less">
</style>



