<template>
<div class="content-wrapper">
  <el-breadcrumb separator="/">
    <el-breadcrumb-item :to="{ path: '/' }">首页</el-breadcrumb-item>
    <el-breadcrumb-item>{{$route.params.sub}}</el-breadcrumb-item>
    <el-breadcrumb-item>{{$route.params.id}}</el-breadcrumb-item>
  </el-breadcrumb>
  <div :is="gridPath"></div>
</div>
</template>

<script>
import projectGrid from "cps/features/project/grid.vue"
import documentGrid from "cps/features/document/grid.vue"
export default {
  components:{
    "projectGrid":projectGrid,
    "documentGrid":documentGrid
  },
  data() {
    return {
      gridPath:"projectGrid"
    }
  },
  watch: {
    '$route'(to, from) {
			this.$timer.set("tempTreeload",()=>{
        //this.gridPath=this.$route.params.sub;
        console.log(this.$route.params.sub)
				//console.log(this.tempTree);
			},500)
		}
  },
  methods: {
  },
  mounted() {
    console.log(this.$route.params.sub+"Grid"+this.$route.params.id)
  }
};
</script>



