<template>
  <el-card shadow="hover" class="portlet-card portlet-toolbox">
    <div slot="header" class="clearfix">
      <span class="title">常用工具</span>
      <el-button class="ctrl-btn" type="text">查看列表 <i class="fal fa-angle-double-right"></i></el-button>
    </div>
    <ul class="toolbox">
        <li v-for="(item,index) in tools" :key="'tools'+index">
            <i :class="item.icon"><span class="badge bg-major" v-if="item.badge">{{item.badge}}</span></i>
            <span class="desc">{{item.name}}</span>
        </li>
    </ul>
  </el-card>
</template>

<script>
export default {
  name: 'app',
  data () {
    return {
      tools:[
        {name:"通讯录",icon:"fa fa-address-book",badge:""},
        {name:"待办工作",icon:"fa fa-tasks",badge:"3"},
        {name:"Email",icon:"fa fa-envelope",badge:""},
        {name:"地图",icon:"fa fa-map",badge:""},
        {name:"新闻",icon:"fa fa-newspaper",badge:""},
        {name:"及时通讯",icon:"fa fa-comments",badge:"25"},
        {name:"便签",icon:"fa fa-sticky-note"},
      ]
    }
  }
}
</script>

<style lang="less">

</style>



