<template>
  <div class="tree-wrapper">
  	<el-input placeholder="输入关键字进行过滤" size="small" v-model="filterText">
  		<i slot="prefix" class="fa fa-filter"></i>
  	</el-input>
  	<el-tree :data="tree"  node-key="id"  @node-click="handleNodeClick" :props="defaultProps" :filter-node-method="filterNode" ref="asideTree"><!--accordion-->
  		<span class="custom-tree-node" slot-scope="{ node, data }" :class="data.children?'has-child':'no-child'">
	        <i class="tree-icon" :class="node.icon"></i>{{ node.label }}
	    </span>
  	</el-tree>
  </div>
</template>

<script>
export default {
    data() {
      return {
      	filterText: '',
        tree:[],
        defaultProps: {
          children: 'children',
          label: 'label'
        },
        paramsSub:""
      };
		},
		computed:{},
    watch: {
      filterText(val) {
        this.$refs.asideTree.filter(val);
      },
      '$route'(to, from) {
				if(this.paramsSub!=this.$route.params.sub){//比对记录的:sub参数是否等于现在的参数，防止重复getData
					//this.getData();
					//console.log(this.tree);
					this.$timer.set("treeload",()=>{
						this.tree=this.$vuex.state.header.nav[this.paramsSub].children;
						//console.log(this.$vuex.state.header.nav[this.paramsSub].children);
					},500)
					this.paramsSub=this.$route.params.sub;
					this.$storage.del("treeLastClick");//点击上层菜单，检测到sub参数改变后说明切换了主菜单，这时删除记录的tree节点
					//console.log("changed",this.$storage.get("treeLastClick"))
				}else{
					
					//console.log("nochange",this.$storage.get("treeLastClick"))
				}
	  	}
    },
    methods: {
			/*
			getData(){
				var url=this.$urls.header;
				this.$http.get(url).then(response=>{
							var data= JSON.parse(response.bodyText);
							this.tree=data.nav[this.$route.params.sub].children;
							//console.log(data.nav[this.$route.params.id].children)
				},err=>{
					
				})
	  	},*/
      filterNode(value, data) {
        if (!value) return true;
        return data.label.indexOf(value) !== -1;
      },
      handleNodeClick(tJson,tNode,tComps){
      	if(!tJson.children){//无二级菜单的情况下才执行roter跳转
      		this.$router.push({path:tJson.url});
      		this.$storage.set("treeLastClick",tNode.key);
      		//[i].expanded=this.isexpand
      		//console.log(this.$vuex.state.tree.lastClick);
      		/*以下这段js是异步解决点击侧边无法激活active的问题，不知道为什么现在好了....
      		this.$timer.set("setCurrent",()=>{
		   		this.$refs.asideTree.setCurrentNode(tJson);
		   	//this.$refs.asideTree.setCurrentKey(tNode.key);
		   	//console.log(this.$refs.asideTree.getCurrentNode());
		   },500)*/
		  /*this.$nextTick(() => {
			  this.$refs.treeBox.setCurrentKey(value); // treeBox 元素的ref   value 绑定的node-key
			});*/
		  
      	}
	  }
    },
    mounted(){
				//this.getData();
				this.paramsSub=this.$route.params.sub;//记录:sub参数，供切换路由时候做对比使用。
				this.$timer.set("treeload",()=>{
					this.tree=this.$vuex.state.header.nav[this.paramsSub].children;
					//console.log(this.$vuex.state.header.nav[this.paramsSub].children);
				},500)
    		this.$timer.set("treeActive",()=>{//刷新后判断上次点击的treeNode并展开激活，用了递归逐级展开。
					//this.tree=this.$vuex.state.header.nav[this.paramsSub].children;
					//console.log(this.$storage.get("treeLastClick"));
					if(this.$storage.get("treeLastClick")){
	    			var node=this.$refs.asideTree.getNode(this.$storage.get("treeLastClick")).parent;
	    			var foo=()=>{
			        if(node.parent){
		    				node.expanded=true;
		    				node=node.parent;
		    				foo();
		    			}
			    }
	    			foo();
	    			this.$refs.asideTree.setCurrentKey(this.$storage.get("treeLastClick"));//选中上次点击的treeNode
    			}
  			//console.log(this.$refs.asideTree.getNode(this.$storage.get("treeLastClick")).parent.parent);
    		},1000)
  		//console.log(this.$vuex.state.tree.lastClick);
    		//console.log(this.data2[4].children.length)
    }
  };
</script>

<style lang="less">

</style>



