<template>
    <div id="chart-map" style="height:100%;"></div>
    <!--<el-card shadow="hover" v-if="false" class="portlet-card" style="height:800px"></el-card>-->
</template>
<script>

//import echarts from 'echarts';
import echarts from "static/lib/echarts.min.js";
import chinaJson from "static/lib/china.json";
echarts.registerMap('china', chinaJson);
import $c from "static/lib/ayincolor-vue.js";
 
export default {
  data() {
    return {}
  },
  methods: {},
  mounted() {
		var echartMap = echarts.init(document.getElementById('chart-map'));
        echartMap.showLoading();
        var optionMap="";
        this.$http.get(this.$urls.weiboTest).then(weiboData => {
            //console.log(weiboData.body);
            
            weiboData = weiboData.body.map(function (serieData, idx) {
                var px = serieData[0] / 1000;
                var py = serieData[1] / 1000;
                var res = [[px, py]];

                for (var i = 2; i < serieData.length; i += 2) {
                    var dx = serieData[i] / 1000;
                    var dy = serieData[i + 1] / 1000;
                    var x = px + dx;
                    var y = py + dy;
                    res.push([x.toFixed(2), y.toFixed(2), 1]);
                    px = x;
                    py = y;
                }
                return res;
            });
            
            optionMap = {
                backgroundColor: '#3b435b',
                title : {
                    text: '全国用户访问数据流量图',
                    subtext: '图例数据来自于微博点赞图',
                    sublink: 'http://www.thinkgis.cn/public/sina',
                    left: 'center',
                    top: '20',
                    textStyle: {
                        color: '#fff',
                        fontSize:14
                    }
                },
                tooltip: {},
                legend: {
                    left: '20',
                    top:"20",
                    data: ['强', '中', '弱'],
                    textStyle: {
                        color: '#ccc'
                    }
                },
                geo: {
                    name: '强',
                    type: 'scatter',
                    map: 'china',
                    label: {
                        emphasis: {
                            show: false
                        }
                    },
                    itemStyle: {
                        normal: {
                            areaColor: '#1f2433',
                            borderColor: '#111'
                        },
                        emphasis: {
                            areaColor: '#293044'
                        }
                    }
                },
                series: [{
                    name: '弱',
                    type: 'scatter',
                    coordinateSystem: 'geo',
                    symbolSize: 1,
                    large: true,
                    itemStyle: {
                        normal: {
                            shadowBlur: 2,
                            shadowColor: $c.bll7,
                            color: $c.bll7
                        }
                    },
                    data: weiboData[0]
                }, {
                    name: '中',
                    type: 'scatter',
                    coordinateSystem: 'geo',
                    symbolSize: 1,
                    large: true,
                    itemStyle: {
                        normal: {
                            shadowBlur: 2,
                            shadowColor: $c.bll5,
                            color: $c.bll5
                        }
                    },
                    data: weiboData[1]
                }, {
                    name: '强',
                    type: 'scatter',
                    coordinateSystem: 'geo',
                    symbolSize: 1,
                    large: true,
                    itemStyle: {
                        normal: {
                            shadowBlur: 2,
                            shadowColor: $c.bll3,
                            color: $c.bll3
                        }
                    },
                    data: weiboData[2]
                }]
            }
        }, err => {
            
        })
		this.$timer.set("echartMap",()=>{
            echartMap.setOption(optionMap);
            //echart1.setOption(option1);
            echartMap.hideLoading();
    	    echartMap.resize();	
		},1000)



  }
}
</script>

<style lang="less">

</style>



