<template>
  <div id="app">
	<component :is="$vuex.state.theme.layout"></component>
	<!--<v-login></v-login>--><!--本页面仅作判断主题系统配置等公共操作之用-->
	<el-dialog title="请选择主题" :visible.sync="$vuex.state.theme.dialogShow" width="550px" :before-close="handleClose">
    <div class="theme-list">
      <el-radio v-model="$vuex.state.theme.layout" v-for="(item,index) in $vuex.state.theme.list" :label="item.layout" :key="index" border><img :src="item.img" alt="" />{{item.name}}</el-radio>
    </div>
    <span slot="footer" class="dialog-footer">
      <el-button type="primary" @click="$vuex.state.theme.dialogShow = false">关 闭</el-button>
    </span>
    </el-dialog>
  </div>
</template>

<script>
import layoutLegend from "cps/layout/layout-legend/index-layout-legend.vue";
import layoutEnlighten from "cps/layout/layout-enlighten/index-layout-enlighten.vue";
import layoutBeyond from "cps/layout/layout-beyond/index-layout-beyond.vue";

export default {
  name: 'app',
  data(){
  	return{
  	}
  },
  components:{
  	"layoutLegend":layoutLegend,
  	"layoutEnlighten":layoutEnlighten,
  	"layoutBeyond":layoutBeyond
  },
  methods:{
  	handleClose(done){
        done();
    }
  },
  updated(){
	if(!this.$handleClass.judge(document.getElementById("html"),this.$vuex.state.theme.layout)){
		this.$vuex.commit("themeChange");//判断body的class是否包含vuex中的theme值，然后执行vuex中的themeChange
	}
  },
  mounted(){
  	if(this.$storage.get("theme-layout")){
  		this.$vuex.state.theme.layout=this.$storage.get("theme-layout");
  	}
  	//console.log(this.$storage.get("theme-layout"));
  }

}
</script>
<style lang="less" scoped="scoped">
</style>



