import Vue from 'vue'
import Vuex from 'vuex'
Vue.use(Vuex)
import {handleClass,storage,session} from "./model/common.js";
import {urls} from "./urls.js";
import router from './router.js'

const store=new Vuex.Store({
	state:{//1.state在vuex中用于存储数据
		switch:{
			logoHover:false,
			maximize:false,
		  	searchFocus:false,
		  	asideIsCollapse:false
		},
		theme:{
			dialogShow: false,
			layout:"layoutLegend",
			lastLayout:"",
			list:[
				{name:"传承",layout:"layoutLegend",img:"static/images/theme/layout-legend.png"},
				{name:"启发",layout:"layoutEnlighten",img:"static/images/theme/layout-enlighten.png"},
				{name:"超越",layout:"layoutBeyond",img:"static/images/theme/layout-beyond.png"}
				//{name:"未来",layout:"layoutFeture"},
			]
		},
		header:{
			nav:[],
			userInfo:[],
			message:[],
			searchInput:'',
			logo:{
				img:"static/images/logo.png",
				chs:"东华软件项目管理系统",
				eng:"DHCC Project Management System"
			}
		},
		loginInfo:{
			logged:false,
			root:""
		}
	},
	mutations:{//2.mutations 里面放的方法，用于改变state的数据
		themeChange(state){
			handleClass.add(document.getElementById("html"),state.theme.layout);
			handleClass.del(document.getElementById("html"),state.theme.lastLayout);
			state.theme.lastLayout=state.theme.layout;
			storage.set("theme-layout",state.theme.layout);
			//console.log(state);
		},
		getHeader(state){
			var url=urls.header;
			Vue.http.get(url).then((response)=>{
	         	var data=JSON.parse(response.bodyText);
	         	state.header.nav=data.nav;
		        state.header.userInfo=data.userInfo;
			    state.header.message=data.message;
			    //console.log(state.homeNav)
			},error=>{
				//console.log(err);
			}).then(result => {
			    //console.log(result);
			})
		},
		clsTreeState(){
			storage.del("treeLastClick");//传承 超越 布局的导航点击的时候清除treeLastClick
			//console.log("clsTreeState")
		},
		isLogin (state,user){
			if(user){
				state.loginInfo.currentUser=user;
				state.logged="true"
			}else if(user==null){
				//登出晴空sessionStorage
				session.del("userName");
				//session.del("userToken");
				state.loginInfo.currentUser=null;
				state.loginInfo.logged="false";
				state.loginInfo.token="";
				
			}
			state.user = user;
		},
		isLogin(state){//废弃，被router的全局守卫接管。
			//console.log(session.get('userid')!="");
      if (!state.loginInfo.logged) {
        //router.push('/login');
			}
			//console.log(state.loginInfo.root,state.loginInfo.logged);
      //console.log(this.$session.get('userid'),this.$session.get('userinfo'))
      //console.log(this.$cookie.get('userid'));
		},
		logout(state){
			session.del('userid');
			state.loginInfo.logged=false;
			state.loginInfo.root="";
			setTimeout(function(){
				router.push({path: '/login'});
			}.bind(this),1000)
		}
	},
	modules: {
      handleClass
	},
	getters:{//类似计算属性，改变state里面count属性，会触发getters的方法，获取新值
		computed(state){
			console.log("getters");
			//return state.count*2
		}
	},
	actions:{//异步
		incMutationsCount(context){
			context.commit("incCount");//执行mutations里面的incCount
		},
		setLogin({commit,state}){
			let sessionUserid = session.get('userid');
			if (sessionUserid!="") {
				state.loginInfo.logged=true;
				state.loginInfo.root=session.get('userinfo');
			}
			//console.log(session.get('userid'));
			//console.log(state.loginInfo.logged,state.loginInfo.root)
			//commit("isLogin");
		}
	}
})

export default store;


