<template>
<ul class="assist">
  <li>
    <div class="assist-search" :class="$vuex.state.switch.searchFocus?'search-in':'search-out'" :style="'width:'+searchWidth+'px'">
      <input class="as-input" type="text"  v-model="$vuex.state.header.searchInput" @focusin="$vuex.state.switch.searchFocus=true" @focusout="$vuex.state.switch.searchFocus=false" placeholder="请输入关键字..."/>
      <button class="btn"><i class="fa fa-search"></i></button>
    </div>
  </li>
  <li>
    <el-dropdown placement="top" class="user-menu" trigger="click">
      <!--top/top-start/top-end/bottom/bottom-start/bottom-end-->
      <span class="el-dropdown-link">
	      	<img class="head" :src="userAvatar" alt="">
            <span class="name" :data-userid="userID">{{userName}}</span><i class="el-icon-arrow-down el-icon--right"></i>
      </span>
      <el-dropdown-menu slot="dropdown" class="header-dropdown">
        <el-dropdown-item v-for="(item,index) in userMenu" :disabled="item.disabled" :key="index" :divided="item.divided">
					<div @click="userMenuAct(item.methods)">{{item.desc}}</div>
				</el-dropdown-item>
      </el-dropdown-menu>
    </el-dropdown>
  </li>
  <li>
    <el-dropdown placement="top" class="message" trigger="click">
      <el-tooltip effect="light" :manual="true" :value="msgShow" content="新通知！" placement="bottom">
        <span class="el-dropdown-link">		      	<span class="bell" :class="msgAni?'animated swing infinite':'aniDis'"><i class="fal fa-bell"></i></span>
        <span class="count" v-if="messageAll>0">{{messageAll}}</span>
        <span class="badge alarm-bg level-3"  v-if="$vuex.state.header.message['level-3']&&$vuex.state.header.message['level-3'].count>0">{{$vuex.state.header.message['level-3'].count}}</span>
        </span>
      </el-tooltip>
      <el-dropdown-menu slot="dropdown" class="header-dropdown">
        <el-dropdown-item v-for="(item,index) in $vuex.state.header.message" :key="index">{{item.desc}}<span class="badge alarm-bg" :class="index">{{item.count}}</span></el-dropdown-item>
        <el-dropdown-item divided="divided"><span @click="clsMsg()">关闭通知</span></el-dropdown-item>
      </el-dropdown-menu>
    </el-dropdown>
  </li>
  <li>
    <el-dropdown placement="top" class="sys-menu" trigger="click">
      <span class="el-dropdown-link">
            <i class="fal fa-bars"></i>
	      </span>
      <el-dropdown-menu slot="dropdown" class="header-dropdown">
        <el-dropdown-item v-for="(item,index) in sysMenu" :disabled="item.disabled" :key="index" :divided="item.divided">
          <div @click="sysMenuAct(item.methods)">{{item.desc}}</div>
        </el-dropdown-item>
      </el-dropdown-menu>
    </el-dropdown>
  </li>
  <li @click="$vuex.state.switch.maximize=!$vuex.state.switch.maximize">
    <el-tooltip effect="light" content="最大化" :open-delay="1500" placement="left">
      <i class="fal fa-expand-wide" v-if="!$vuex.state.switch.maximize"></i>
      <i class="fal fa-compress-wide" v-if="$vuex.state.switch.maximize"></i>
    </el-tooltip>
  </li>
</ul>
</template>

<script>
export default {
  props: ['searchWidth'],
  data() {
    return {
      msgAni: true,
      msgShow: false,
      userMenu: [{
					desc: "个人资料",
					methods: "personal"
        },
        {
					desc: "安全",
					methods: "secure",
          disabled: "disabled"
        },
        {
          desc: "个性化",
					methods: "personalise"
        },
        {
          desc: "注销",
					methods: "exit",
          divided: "divided"
        },
      ],
      sysMenu: [{
          desc: "门户参数",
          methods: "setting",
          disabled: "disabled"
        },
        {
          desc: "主题设置",
          methods: "theme"
        },
        {
          desc: "编辑布局",
          methods: "layout",
          disabled: "disabled"
        },
        {
          desc: "颜色表",
          methods: "ayincolor"
        },
        {
          desc: "退出系统",
          methods: "exit",
          divided: "divided"
        }
      ]
    }
  },
  computed: {
    messageAll: function() {
      var message = this.$vuex.state.header.message,
        msgLength = Object.keys(message).length;
      var MAll = 0;
      for (var i = 0; i < msgLength; i++) {
        MAll = message["level-" + i].count + MAll;
      }
      if (MAll > 0) {
        this.$timer.set("msgShow", () => {
          this.msgShow = true;
          this.$timer.set("msgHide", () => {
            this.msgShow = false;
          }, 5000)
        }, 1000)
      }
      return MAll;
    },
    userAvatar:function(){
      return "static/images/head/"+this.$vuex.state.loginInfo.root.avatar;
    },
    userID:function(){
      return this.$vuex.state.loginInfo.root.userid;
    },
    userName:function(){
      var name=this.$vuex.state.loginInfo.root.username
      if(name){
        return name;
      }else{
        return this.$vuex.state.loginInfo.root.userid;
      } 
    }
  },
  methods: {
    sysMenuAct(val) {
      if (val == "theme") {
        this.$vuex.state.theme.dialogShow = true; //改写vuex中的值，让其显示在上层组件
      }else if(val=="exit"){
				this.logout();
			}else if(val=="ayincolor"){
        this.$router.push({path: '/ayincolor'});
      }
		},
		userMenuAct(val){
			if (val == "personal") {
        alert("个人菜单，暂未开发")
      }else if(val=="exit"){
				this.logout();
			}
		},
    clsMsg() {
      //console.log(this.msgAni);
      var message = this.$vuex.state.header.message;
      this.msgAni = !this.msgAni;
      var msgLength = Object.keys(message).length;
      for (var i = 0; i < msgLength; i++) {
        message["level-" + i].count = 0;
      }
    },
    logout() {
      this.$vuex.commit("logout");
      this.$message({
				message: '注销成功！',
				type: 'info',
				duration:1000
			});
    }
  },
  mounted() {
    //console.log(this.$vuex.state.login);
  }
}
</script>



