// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
import Vue from 'vue'
import App from './App'

import ElementUI from 'element-ui';
import 'element-ui/lib/theme-chalk/index.css';
Vue.use(ElementUI);

import VueResource from "vue-resource"
Vue.use(VueResource);

import router from './router.js'
import 'assets/less/main.less';
Vue.config.productionTip = false

import vuex from "./vuex.js";
Vue.prototype.$vuex = vuex;
import {storage,session,cookie,timer,handleClass,BPInfo} from "./model/common.js";//引入自己封装好的js
Vue.prototype.$timer = timer;//操作计时器的方法，添加到vue属性中后用this.$timer调用！
Vue.prototype.$storage = storage;//操作本地storage的方法,不同于cookie
Vue.prototype.$session = session;//操作本地session的方法
Vue.prototype.$cookie = cookie;//操作本地cookie的方法
Vue.prototype.$handleClass = handleClass;//特殊情况下操作dom的class,如body、html的class无法数据绑定的方式操作
Vue.prototype.$BPInfo = BPInfo;//特殊情况下操作dom的class,如body、html的class无法数据绑定的方式操作
//import {datap} from "./datap.js";
//Vue.prototype.$datap = datap;
import {urls} from "./urls.js";
Vue.prototype.$urls = urls;

//import Icon from 'vue-svg-icon/Icon.vue';
import Icon from './model/Icon.vue';//在vue-svg-icon的基础上二次修改，作为一个自有组件引入
Vue.component('icon', Icon); 

//加载fontawesomePro,webfont的方式
import 'fa/less/fontawesome.less';
import 'fa/less/light.less';
import 'fa/less/solid.less';
//import 'fa/less/regular.less';
//import 'fa/less/brands.less';
/*
//加载fontawesomePro，SVG.js的方式
import fontawesome from "fa/js/fontawesome.js"
import fal from "fa/js/light.js"
import far from "fa/js/regular.js"
import fas from "fa/js/solid.js"
import fab from "fa/js/brands.js"
*/
new Vue({
  el: '#app',
  router,
  components: { App },
  template: '<App/>'
})



