<template>
<el-card shadow="hover" class="portlet-card">
    <div slot="header" class="clearfix">
      <span class="title">项目统计</span>
      <el-button class="ctrl-btn" type="text">查看列表 <i class="fal fa-angle-double-right"></i></el-button>
    </div>
    <div id="chart-gauge01" style="height:280px;"></div>
</el-card>
</template>

<script>

//import echarts from 'echarts';
import echarts from "static/lib/echarts.min.js"
import $c from "static/lib/ayincolor-vue.js"
 
export default {
  data() {
    return {}
  },
  methods: {},
  mounted() {
		//this.$timer.set("test",()=>{
		//},3000)
		//console.log($c.rel5);
    
        var echartGauge01 = echarts.init(document.getElementById('chart-gauge01'));
        var optionGauge01 = {
            
            tooltip : {
                formatter: "{a} <br/>{b} : {c}%"
            },
            toolbox: {
                feature: {
                    restore: {},
                    saveAsImage: {}
                }
            },
            series: [
                {
                    name: '业务指标',
                    type: 'gauge',
                    axisLine: {lineStyle: { color: [ [0.7, $c.chl5],[.8, $c.yel5],[1, $c.orl5]]}},
                    axisLabel: {textStyle: {color: $c.gyl5}},
                    axisTick: {lineStyle: {color: $c.wh}},
                    splitLine: {lineStyle: {color: $c.wh}},
                    //startAngle: 269,
                    //endAngle: -90,
                    radius: '100%',
                    center: ['50%', '60%'],
                    detail: {
                        formatter:'{value}%',
                        textStyle: {
                            color: $c.gyr5,
                            fontSize: 28
                        }
                    },
                    data: [{value: 63}]
                }
            ]
        };
		
		this.$timer.set("echartGauge01",()=>{
			echartGauge01.setOption(optionGauge01);
    	    echartGauge01.resize();	
		},1000)



  }
}
</script>

<style lang="less">

</style>



