<template>
<el-form ref="task" class="dialog-form dialog-form-edit" :model="newtask" label-width="90px" style="width:100%;">
  
  <el-row>
    <el-col :span="16">
      <el-form-item label="所属任务">
        <el-input v-model="newtask.title"></el-input>
      </el-form-item>
    </el-col>
    <el-col :span="8">
      <el-form-item label="所属阶段">
        <el-input v-model="newtask.endData"></el-input>
      </el-form-item>
    </el-col>
  </el-row>

  <el-row>
    <el-col :span="24">
      <el-form-item label="备注">
        <el-input type="textarea" v-model="newtask.desc" rows="2"></el-input>
      </el-form-item>
    </el-col>
  </el-row>

  <el-row>
    <el-col :span="24">
      <el-form-item label="上传文件">
        <el-upload class="form-upload" :file-list="fileList" drag action="https://jsonplaceholder.typicode.com/posts/" multiple>
          <i class="el-icon-upload"></i>
          <div class="el-upload__text">将文件拖到此处，或<em>点击上传</em></div>
          <div class="el-upload__tip" slot="tip">可以上传 doc/excle/ppt/xmind/jpg/png 等文件，且不超过10MB</div>
        </el-upload>
      </el-form-item>
    </el-col>
  </el-row>

</el-form>
</template>

<script>
export default {
  props: [],
  data() {
    return {
      fileList: [{
        name: 'food.jpeg',
        url: 'https://fuss10.elemecdn.com/3/63/4e7f3a15429bfda99bce42a18cdd1jpeg.jpeg?imageMogr2/thumbnail/360x360/format/webp/quality/100'
      }, {
        name: 'food2.jpeg',
        url: 'https://fuss10.elemecdn.com/3/63/4e7f3a15429bfda99bce42a18cdd1jpeg.jpeg?imageMogr2/thumbnail/360x360/format/webp/quality/100'
      }],
      formLabelWidth: '120px',
      newtask: {
        title: '',
        publisher: '',
        excepter: '',
        startDate: '',
        endData: '',
        type: '',
        level: '',
        type: '',
        details: '',
        desc: ''
      }
    };
  },
  watch: {

  },
  methods: {

  },
  mounted() {

  }
};
</script>



