<template>
<el-card shadow="hover" class="portlet-card">
  <div slot="header" class="clearfix">
    <span class="title">项目统计</span>
    <el-button class="ctrl-btn" type="text">查看列表 <i class="fal fa-angle-double-right"></i></el-button>
  </div>
  <div id="chart-bar02" style="height:280px;"></div>
</el-card>
</template>

<script>

//import echarts from 'echarts';
import echarts from "static/lib/echarts.min.js"
import $c from "static/lib/ayincolor-vue.js"
 
export default {
  data() {
    return {}
  },
  methods: {},
  mounted() {
		//this.$timer.set("test",()=>{
		//},3000)
		//console.log($c.rel5);
    var echartBar02 = echarts.init(document.getElementById('chart-bar02'));
    var xAxisData = [];
    var data1 = [];
    var data2 = [];
    for (var i = 0; i < 100; i++) {
        xAxisData.push('' + i);
        data1.push((Math.sin(i / 5) * (i / 5 -10) + i / 6) * 5);
        data2.push((Math.cos(i / 5) * (i / 5 -10) + i / 6) * 5);
    }

    var optionBar02 = {
        legend: {
            data: ['业务一', '业务二'],
            align: 'left',
            top:'5px',
            textStyle:{
                color:$c.gyr5
            }
        },
        grid: {
            left: '2%',
            right: '2%',
            bottom: '4%',
            top:"15%",
            containLabel: true
        },
        toolbox: {
            // y: 'bottom',
            feature: {
                magicType: {
                    type: ['line', 'bar','stack', 'tiled']
                },
                dataView: {},
                saveAsImage: {
                    pixelRatio: 2
                }
            }
        },
        tooltip: {},
        xAxis: {
            data: xAxisData,
            silent: false,
            splitLine: {
                show: false
            },
            axisLine: {
                lineStyle: {
                    color:$c.gyl3
                },
            },
            axisTick: {
                lineStyle: {
                    color:$c.gyl3
                },
            },
            axisLabel : {
                textStyle : {
                    color :$c.gyd5,
                    align : 'center'
                }
            }
        },
        yAxis: {
            axisLine: {
                lineStyle: {
                    color:$c.gyl3
                },
            },
            axisTick: {
                lineStyle: {
                    color:$c.gyl3
                },
            },
            axisLabel : {
                textStyle : {
                    color :$c.gyd5,
                    align : 'right'
                }
            }
        },
        series: [{
            name: '业务一',
            type: 'bar',
            data: data1,
            stack:false,
            itemStyle:{normal:{color:$c.bll4}},
            animationDelay: function (idx) {
                return idx * 10;
            }
        }, {
            name: '业务二',
            type: 'bar',
            data: data2,
            stack:false,
            itemStyle:{normal:{color:$c.bll6}},
            animationDelay: function (idx) {
                return idx * 10 + 100;
            }
        }],
        animationEasing: 'elasticOut',
        animationDelayUpdate: function (idx) {
            return idx * 5;
        }
    };
		
		this.$timer.set("echartBar02",()=>{
			echartBar02.setOption(optionBar02);
    	echartBar02.resize();	
		},1000)



  }
}
</script>

<style lang="less">

</style>



