var timer={
	data:{},
	set:function(s, fn, ms, e){timer.clear(s);timer.data[s]=setTimeout(fn, ms ,e);},
	clear:function(s){var t=timer.data; if(t[s]){clearTimeout(t[s]);delete t[s];}}
}
//把模块暴露出去


var storage={//
	set(key,value){
		localStorage.setItem(key,JSON.stringify(value))
	},
	get(key){
		return JSON.parse(localStorage.getItem(key));
	},
	del(key){
		localStorage.removeItem(key);
	}
}

var session={
	set(key,items){
		sessionStorage.setItem(key,JSON.stringify(items));
	},
	get(key){
		return JSON.parse(sessionStorage.getItem(key) || "[]")
	},
	del(key){
		sessionStorage.removeItem(key);
	}
}

var cookie = {
    set(c_name, value, expire) {
        var date = new Date()
        date.setSeconds(date.getSeconds() + expire)
        document.cookie = c_name + "=" + escape(value) + "; expires=" + date.toGMTString()
        //console.log(document.cookie)
    },
    get(c_name){
        if (document.cookie.length > 0) {
            let c_start = document.cookie.indexOf(c_name + "=")
            if (c_start != -1) {
                c_start = c_start + c_name.length + 1
                let c_end = document.cookie.indexOf(";", c_start)
                if (c_end == -1) c_end = document.cookie.length
                return unescape(document.cookie.substring(c_start, c_end))
            }
        }
        return ""
    },
    del(c_name) {
        cookie.set(c_name, "", -1)
    }
}

var handleClass={
	has(obj, cls){
	    return obj.className.match(new RegExp('(\\s|^)' + cls + '(\\s|$)'));  
	},
	judge(obj,cls){
		return !!this.has(obj, cls);
	},
	add(obj, cls){  
	    if (!this.has(obj, cls)) obj.className += " " + cls;  
	},
	del(obj, cls){  
	    if (this.has(obj, cls)) {  
	        var reg = new RegExp('(\\s|^)' + cls + '(\\s|$)');  
	        obj.className = obj.className.replace(reg, ' ');  
	    }  
	},
	toggle(obj,cls){  
	    if(this.has(obj,cls)){  
	        this.del(obj, cls);  
	    }else{  
	        this.add(obj, cls);  
	    }  
	}
}


var BPInfo={
	uaMatch( ua ) {
	    ua = ua.toLowerCase();
	    var match = /(opr)[\/]([\w.]+)/.exec( ua ) ||
	        /(chrome)[ \/]([\w.]+)/.exec( ua ) ||
	        /(version)[ \/]([\w.]+).*(safari)[ \/]([\w.]+)/.exec( ua ) ||
	        /(webkit)[ \/]([\w.]+)/.exec( ua ) ||
	        /(opera)(?:.*version|)[ \/]([\w.]+)/.exec( ua ) ||
	        /(msie) ([\w.]+)/.exec( ua ) ||
	        ua.indexOf("trident") >= 0 && /(rv)(?::| )([\w.]+)/.exec( ua ) ||
	        ua.indexOf("compatible") < 0 && /(mozilla)(?:.*? rv:([\w.]+)|)/.exec( ua ) ||
	        [];
	    var platform_match = /(ipad)/.exec( ua ) ||
	        /(iphone)/.exec( ua ) ||
	        /(android)/.exec( ua ) ||
	        /(windows phone)/.exec( ua ) ||
	        /(win)/.exec( ua ) ||
	        /(mac)/.exec( ua ) ||
	        /(linux)/.exec( ua ) ||
	        /(cros)/i.exec( ua ) ||
	        [];
	    return {
	        browser: match[ 3 ] || match[ 1 ] || "",
	        version: match[ 2 ] || "0",
	        platform: platform_match[ 0 ] || ""
	    };
	},
    get(){
	  var matched = BPInfo.uaMatch( window.navigator.userAgent );
	  var browser = {};
	  if ( matched.browser ) {
	    browser[ matched.browser ] = true;
	    browser.version = matched.version;
	    browser.versionNumber = parseInt(matched.version);
	  }
	  if ( matched.platform ) {
	    browser[ matched.platform ] = true;
	  }
	  if ( browser.android || browser.ipad || browser.iphone || browser[ "windows phone" ] ) {
	    browser.mobile = true;
	  }
	  if ( browser.cros || browser.mac || browser.linux || browser.win ) {
	    browser.desktop = true;
	  }
	  if ( browser.chrome || browser.opr || browser.safari ) {
	    browser.webkit = true;
	  }
	  if ( browser.rv )
	  {
	    var ie = "msie";
	    matched.browser = ie;
	    browser[ie] = true;
	  }
	  if ( browser.opr )
	  {
	    var opera = "opera";
	    matched.browser = opera;
	    browser[opera] = true;
	  }
	  if ( browser.safari && browser.android )
	  {
	    var android = "android";
	    matched.browser = android;
	    browser[android] = true;
	  }
	  browser.name = matched.browser;
	  browser.platform = matched.platform;
	  var $browser = browser;
	  var judgeBE=function(){
			if($browser.msie){
			    var bv=parseInt($browser.version);
			    if(bv==7&&navigator.appVersion.indexOf("Trident\/4.0")>0){bv=8}
			    $("html").data("bv",bv);
			    return "IE "+"IE"+bv;}
			else if($browser.safari){return "safari webkit";}
			else if($browser.chrome){return "chrome webkit";}
			else if($browser.opera){return "opera webkit";}
			else if($browser.mozilla){return "mozilla";}
		}
		
		var judgePF=function(){
	        var x="";
	        if($browser.ipad){x=x+" ipad"}
	        else if($browser.iphone){x=x+" iphone"}
	        else if($browser["windows phone"]){x=x+" winphone"}
	        else if($browser.android){x=x+" android"}
	        else if($browser.win){x=x+" win"}
	        else if($browser.mac){x=x+" mac"}
	        else if($browser.linux){x=x+" linux"}
	        else if($browser.cros){x=x+" cros"}
	        
	        if($browser.desktop){x=x+" desktop"}
	        else if($browser.mobile){x=x+" mobile"}
	        return x;
	    }
		return judgeBE()+judgePF()+" UI&UE&FE.by.ayin86(at)163.com";
	}
	
}

//把模块暴露出去
export {storage,session,cookie,timer,handleClass,BPInfo};


